import React from "react";
import {
  Button,
  FormControlLabel,
  Checkbox,
  FormGroup,
} from "@material-ui/core";
import { TIER_LIST } from "./../../model/blueprint-model";
import { Formik, Form, FieldArray } from "formik";

const TierListForm = ({ handleDialogSubmit }) => {
  const formValidation = () => {};

  const onFormSubmit = (values, actions) => {
    actions.setSubmitting(true);
    handleDialogSubmit(values);
    actions.setSubmitting(false);
  };

  const renderCheckbox = (arrayHelpers, tier, index) => {
    const label = `Tier ${tier.name}`;

    return (
      <FormControlLabel
        key={tier.key}
        label={label}
        control={
          <Checkbox
            value={tier.name}
            checked={tier.value}
            onChange={(e) => {
              tier.value = e.target.checked;
              arrayHelpers.replace(index, tier);
            }}
          ></Checkbox>
        }
      ></FormControlLabel>
    );
  };

  return (
    <Formik
      initialValues={TIER_LIST}
      validate={(values) => formValidation(values)}
      onSubmit={(values, actions) => onFormSubmit(values, actions)}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
        isSubmitting,
      }) => (
        <Form onSubmit={handleSubmit}>
          <FieldArray
            name="tiers"
            render={(arrayHelpers) => (
              <FormGroup>
                {TIER_LIST.map((tier, index) => {
                  return renderCheckbox(arrayHelpers, tier, index);
                })}
              </FormGroup>
            )}
          />

          <div>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              disabled={isSubmitting}
            >
              Apply
            </Button>
          </div>
        </Form>
      )}
    </Formik>
  );
};

export default TierListForm;
