import React, { Fragment, useEffect, useState } from "react";
import { Formik, Field, Form } from "formik";
import {
  calculateSmallTalkEnergy,
  calculateSmallTalkEffciencyData,
} from "./../model/energy-model";
import {
  BarChart,
  Bar,
  Cell,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
} from "recharts";
import {
  Avatar,
  ListItem,
  ListItemText,
  ListItemAvatar,
  TextField,
  Button,
  CssBaseline,
  Container,
  Box,
} from "@material-ui/core";

import ColorGradientGenerator from "./../helper/color-gradient-generator";
import * as Yup from "yup";

const colorGenerator = ColorGradientGenerator("#1C3041", "#18F2B2", 100);

const EnergyCalculator = () => {
  const [formData, setFormData] = useState({
    currentEenergy: 0,
    maxEnergy: 200,
  });
  const [smallTalkEnergy, setSmallTalkEnergy] = useState([]);
  const [smallTalkData, setSmallTalkData] = useState([]);

  useEffect(() => {
    setSmallTalkEnergy(
      calculateSmallTalkEnergy(formData.currentEenergy, formData.maxEnergy)
    );

    setSmallTalkData(
      calculateSmallTalkEffciencyData(
        formData.maxEnergy,
        Math.floor(formData.maxEnergy / 20)
      )
    );
  }, [formData]);

  const onFormSubmit = (values, actions) => {
    actions.setSubmitting(true);
    setFormData(values);
    console.log(values);
    actions.setSubmitting(false);
  };

  const getFillColor = (uv) => {
    const i = Math.floor(uv);
    return "#" + colorGenerator.colorAt(i);
  };

  const EnergySchema = Yup.object().shape({
    currentEnergy: Yup.number().min(0).max(100000).required("Required"),
    maxEnergy: Yup.number().min(0).max(100000).required("Required"),
  });

  const renderForm = (
    <Formik
      initialValues={{
        currentEnergy: 0,
        maxEnergy: 200,
      }}
      onSubmit={onFormSubmit}
      validationSchema={EnergySchema}
    >
      {({
        values,
        errors,
        touched,
        isValidating,
        handleChange,
        handleBlur,
        handleSubmit,
        isSubmitting,
      }) => (
        <Form onSubmit={handleSubmit}>
          <div>
            <Field
              m={4}
              as={TextField}
              variant="outlined"
              type="text"
              name="currentEnergy"
              label="Current Energy"
              required
              error={errors.currentEnergy ? true : false}
              helperText={errors.currentEnergy}
              onFocus={(e) => {
                e.target.select();
              }}
            />

            <Field
              m={4}
              as={TextField}
              variant="outlined"
              type="text"
              name="maxEnergy"
              label="Maximum Energy"
              required
              onFocus={(e) => {
                e.target.select();
              }}
            />
          </div>

          <div>
            <Button
              type="submit"
              vartian="outlined"
              color="primary"
              disabled={isSubmitting}
            >
              Calculate
            </Button>
          </div>
        </Form>
      )}
    </Formik>
  );

  const renderResult = (
    <Fragment>
      <Box component="h3">
        {smallTalkEnergy.isEfficient
          ? "You should small talk."
          : "Do not small talk."}
        {!smallTalkEnergy.isEfficient && smallTalkEnergy.feelingLucky
          ? "Give it a shot if you're feeling lucky."
          : ""}
      </Box>

      <Box textAlign="left" m={1}>
        If successful, you will gain <b>{smallTalkEnergy.gain} energy</b>.
      </Box>

      <Box textAlign="left" m={1}>
        The NPC can backfire <b>{smallTalkEnergy.backfire} energy</b>.
      </Box>
    </Fragment>
  );

  return (
    <Fragment>
      <h2>Small Talk Energy</h2>
      {renderForm}

      <div className="chart-container">
        {smallTalkData.length < 1 ? (
          <Fragment>
            <CssBaseline />
            <Container maxWidth="sm">
              <p>
                Please enter your current and maximum energy to determine
                wheather or not to talk to NPC.
              </p>
            </Container>
          </Fragment>
        ) : (
          <Fragment>
            <BarChart
              data={smallTalkData}
              width={1080}
              height={300}
              margin={{ top: 5, right: 30, left: 20, bottom: 5 }}
            >
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="name" />
              <YAxis domain={[0, 100]} />
              <Tooltip />
              <Legend />
              <Bar dataKey="gain" fill="#82ca9d">
                {smallTalkData.map((data, i) => {
                  const color = getFillColor(data.gain);
                  return <Cell key={i} fill={color} />;
                })}
              </Bar>
            </BarChart>

            {renderResult}
          </Fragment>
        )}
      </div>
    </Fragment>
  );
};

export default EnergyCalculator;
