import React, { useState, useEffect, Fragment } from "react";
import {
  Button,
  Dialog,
  DialogContent,
  DialogTitle,
  DialogActions,
} from "@material-ui/core";

import EditIcon from "@material-ui/icons/Edit";
import BlueprintFormEdit from "../component/blueprint/blueprint-form-edit";

export const BlueprintFormModal = ({ blueprints }) => {
  const [isDialogOpen, setDialogState] = useState(false);
  const [isSubmitting, setSubmitting] = useState(false);

  const getDialogActions = () => {
    return {
      open: () => setDialogState(true),
      close: () => {
        setDialogState(false);
      },
      cancel: () => setDialogState(false),
    };
  };

  return (
    <Fragment>
      <Button
        variant="outlined"
        color="primary"
        onClick={getDialogActions().open}
      >
        Edit <EditIcon/>
      </Button>

      <Dialog open={isDialogOpen} onClose={getDialogActions().close}>
        <DialogTitle>Edit Blueprints</DialogTitle>

        <DialogContent>
          {blueprints.map((blueprint) => {
            return (
              <Fragment>
                <ul>
                  <li>
                    {blueprint.name} [{blueprint.type}]
                  </li>
                  <li>Current Market Value: {blueprint.marketValue}</li>
                </ul>

                <BlueprintFormEdit
                  blueprint={blueprint}
                  dialogActions={getDialogActions()}
                ></BlueprintFormEdit>
              </Fragment>
            );
          })}
        </DialogContent>

        <DialogActions>
          <div>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              disabled={isSubmitting}
            >
              Submit
            </Button>
          </div>
        </DialogActions>
      </Dialog>
    </Fragment>
  );
};

export default BlueprintFormModal;
