/**
 *
 * @param {*} blueprints
 */
export const PIPE_CALCULATION = (blueprints) => {
  return blueprints.map((blueprint) => {
    blueprint.est_market_premium = 1;
    blueprint.est_market_value = blueprint.value * blueprint.est_market_premium;
    blueprint.ratio_value_xp = (
      blueprint.merchantXp / blueprint.est_market_value
    ).toFixed(3);
    return blueprint;
  });
};

/**
 *
 * @param {*} tiers
 * @param {*} page
 */
export const fetchBlueprints = async (tiers, page, itemsPerPage, itemType) => {
  const typesParams = itemType.length > 0 ? getBlueprintTypeParams(itemType) : '';
  const tierParams = tiers.length > 0 ? getTierParams(tiers) : '';
  const url = `http://localhost:80/api/v1/blueprints?order[merchant_xp]=desc${tierParams}${typesParams}&page=${page}&itemsPerPage=${itemsPerPage}`;
  const response = await axios.get(url);

  console.log(response);

  return {
    data: PIPE_CALCULATION(response.data["hydra:member"]),
    totalItems: response.data["hydra:totalItems"],
  };
};

export const fetchItemTypes = async () => {
  const url = 'http://localhost:80/api/v1/item_types';
  const response = await axios.get(url);
  const result = response.data["hydra:member"];

  const itemTypes = result.map((item) => {
    return item.name;
  });

  return sortedUniq(itemTypes);
}

/**
 *
 * @param {*} id
 * @param {*} marketValue
 */
export const patchBlueprints = async (blueprint) => {
  console.log(blueprint);
  const url = `http://localhost:80/api/v1/blueprints/${blueprint.id}`;

  return await axios.patch(url, blueprint, {
    headers: {
      "content-type": 'application/merge-patch+json'
    }
  });
};

export const getTierParams = (tiers) => {  
  return tiers
    .filter((tier) => {
      return tier.value === true;
    })
    .reduce((accumulator, tier) => accumulator + `&tier=${tier.name}`, "");
};

export const getBlueprintTypeParams = (types) => {
  return types
    .filter((type) => {
      return type.value === true;
    })
    .reduce((accumulator, type) => accumulator + `&type=${type.key}`);
};
