import React from "react";
import { Link } from "react-router-dom";
import { List } from "@material-ui/core";

const LINKS = [
  { uri: "/", label: "Blueprints" },
  { uri: "/small-talk", label: "Small Talk" },
  { uri: "/example", label: "Example" },
];

const HomeLink = ({ wrapper }) => {
  const renderLink = () => {
    return LINKS.map((item, i) => {
      return <List key={i}><Link key={i} to={item.uri}>{item.label}</Link></List>;
    });
  };

  return renderLink();
};

export default HomeLink;
