const SMALL_TALK_SUCCESS_RATE = .75;
const LUCK_RATE = .80;

export const calculateSmallTalkEnergy = (currentEnergy, maxEnergy) => {
  const energyGained = calculateEnergyGain(currentEnergy, maxEnergy);
  const maxEnergyGained = calculateMaxEnergyGain(maxEnergy);
  const backfireEnergy = calculateBackfireEnergy(currentEnergy, maxEnergy);
  const efficiency = calculateFirstAttemptEfficiency(
    SMALL_TALK_SUCCESS_RATE,
    energyGained,
    backfireEnergy
  );

  const luckEfficiency = calculateFirstAttemptEfficiency(
    LUCK_RATE,
    energyGained,
    backfireEnergy
  );

  const stats = {
    gain: energyGained,
    maxGain: maxEnergyGained,
    backfire: backfireEnergy,
    isEfficient: efficiency > 49.99,
    feelingLucky: luckEfficiency > 49.99,
    efficiency: efficiency,
  };

  return stats;
};

/**
 * The amount of energy gained.
 */
export const calculateEnergyGain = (currentEnergy, maxEnergy) => {
  let energyGained = 0;

  if (currentEnergy >= maxEnergy) {
    return 0;
  }

  energyGained = (maxEnergy - currentEnergy) / 10;

  return parseFloat(energyGained);
};

export const calculateBackfireEnergy = (currentEnergy, maxEnergy) => {
  if (maxEnergy > 0) {
    const maxBackfireEnergy = Math.floor(maxEnergy / 7);
    const backfireEnergy =
      maxBackfireEnergy >= currentEnergy ? currentEnergy : maxBackfireEnergy;

    return backfireEnergy;
  }

  return 0;
};

/**
 * The maximum amount of of energy to be gained is based on your max energy.
 *
 * @param {*} maxEnergy
 */
export const calculateMaxEnergyGain = (maxEnergy) => {
  if (maxEnergy < 1) {
    return 0;
  }

  return Math.floor(maxEnergy / 10);
};

export const calculateFirstAttemptEfficiency = (
  successRate,
  gainEnergy,
  backfireEnergy
) => {

  const gainEfficiency = gainEnergy * successRate;
  const backfireEfficiency = backfireEnergy * (1 - successRate);

  const exactEfficiency = (gainEfficiency / (gainEfficiency + backfireEfficiency));

  return parseFloat(exactEfficiency * 100).toFixed(2);
};

export const calculateSmallTalkEffciencyData = (maxEnergy, stepSize) => {
  let data = [];
  let smallTalkEnergy = {};

  for (let i = 0; i <= maxEnergy; i += stepSize) {
    smallTalkEnergy = calculateSmallTalkEnergy(i, maxEnergy);

    data.push({
      name: i,
      backfire: 100 - smallTalkEnergy.efficiency,
      gain: smallTalkEnergy.efficiency,
    });
  }

  console.table(data);

  return data;
};
