import React from "react";
import { Route, Link, BrowserRouter as Router, Switch } from "react-router-dom";
import DashboardMainContent from "./../contaner/dashboard-main-content";
import EnergyCalculator from "./../component/energy-calculator";
import NotFound from "./../component/not-found";

const HomeRoute = () => {
  return (
    <Route>
      <div>
        <Switch>
          <Route exact path="/" component={DashboardMainContent} />
          <Route path="/small-talk" component={EnergyCalculator} />
          <Route component={NotFound} />
        </Switch>
      </div>
    </Route>
  );
};

export default HomeRoute;
