import React from "react";
import { Formik, Form } from "formik";
import { Input, Button } from "@material-ui/core";
import { patchBlueprints } from "../../model/blueprint-model";

const formValidation = (values) => {
  const errors = {};
  return errors;
};

const BlueprintFormEdit = ({ blueprint, dialogActions }) => {
  const onFormSubmit = async (values, formikAction) => {
    formikAction.setSubmitting(true);
    const response = patchBlueprints(values);
    formikAction.setSubmitting(false);
    dialogActions.close();
  };

  return (
    <Formik
      initialValues={blueprint}
      validate={(values) => formValidation(values)}
      onSubmit={(values, actions) => onFormSubmit(values, actions)}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
        isSubmitting,
        /* and other goodies */
      }) => (
        <Form onSubmit={handleSubmit}>
          <Input
            type="text"
            name="marketValue"
            onChange={handleChange}
            onBlur={handleBlur}
            value={values.marketValue}
          />
          {errors.email && touched.email && errors.email}
        </Form>
      )}
    </Formik>
  );
};

export default BlueprintFormEdit;
