import React, { useState, useEffect, Fragment } from "react";
import DataTable from "react-data-table-component";
import RefreshIcon from "@material-ui/icons/Refresh";

import {
  TABLE_HEADER,
  fetchBlueprints,
} from "./../model/blueprint-model";

import LoadingCircle from "./../component/loading-circle";
import BlueprintFormModal from "../form/blueprint-form-modal";

const BlueprintView = ({ tiers, itemTypes }) => {
  const [tableData, setTableData] = useState([]);
  const [page, setPage] = useState(1);
  const [totalRows, setTotalRows] = useState(0);
  const [itemsPerPage, setItemsPerPage] = useState(10);
  const [isLoading, setLoading] = useState(false);
  const [selectedRows, updateSelectedRows] = useState();
  const [toggleCleared, setToggleCleared] = useState(false);

  useEffect(() => {
    loadBlueprint();
  }, [page, itemsPerPage, tiers, itemTypes]);

  const loadBlueprint = async () => {
    setLoading(true);
    const blueprints = await fetchBlueprints(
      tiers,
      page,
      itemsPerPage,
      itemTypes
    );

    setTableData(blueprints.data);
    setTotalRows(blueprints.totalItems);

    setLoading(false);
  };

  const handlePageChange = (newPage) => {
    setPage(newPage);
  };

  const handleRowsPerPageChange = (newItemsPerPage, page) => {
    setPage(page);
    setItemsPerPage(newItemsPerPage);
  };

  const contextActions = React.useMemo(() => {
    if (selectedRows) {
      return (
        <BlueprintFormModal
          blueprints={selectedRows.selectedRows}
        ></BlueprintFormModal>
      );
    }
  }, [tableData, selectedRows]);

  return (
    <Fragment>

      <RefreshIcon onClick={loadBlueprint}></RefreshIcon>

      <DataTable
        title="Blueprints"
        columns={TABLE_HEADER}
        data={tableData}
        defaultSortField="value"
        highlightOnHover
        pagination
        paginationServer
        paginationTotalRows={totalRows}
        progressComponent={<LoadingCircle size="75" />}
        progressPending={isLoading}
        selectableRows
        onChangePage={handlePageChange}
        onChangeRowsPerPage={handleRowsPerPageChange}
        onSelectedRowsChange={(newRows) => updateSelectedRows(newRows)}
        contextActions={contextActions}
        clearSelectedRows={toggleCleared}
      />
    </Fragment>
  );
};

export default BlueprintView;
