import React, { useEffect, useState, Fragment } from "react";

import {
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  Grid,
} from "@material-ui/core";

import TierListForm from "../component/blueprint/tier-list-form";
import BlueprintView from "./blueprint-view";
import Question from "./../form/question";
import { TIER_LIST} from "./../model/blueprint-model";
import { BlueprintTypeModal } from "./../form/blueprint-type-modal";

const DashboardMainContent = () => {
  const [isDialogOpen, setDialogState] = useState(false);
  const [tiers, updateTiers] = useState(TIER_LIST);
  const [itemTypes, updateItemTypes] = useState([]);

  const handleDialogOpen = () => {
    setDialogState(true);
  };

  const handleDialogClose = () => {
    setDialogState(false);
  };

  const handleTierFormSubmit = (newTiers) => {
    setDialogState(false);
    updateTiers(newTiers);
    console.log(newTiers);
  };

  const handleItemTypeFormSubmit = (newItemTypes) => {
    setDialogState(false);
    updateItemTypes(newItemTypes);
    console.log(newItemTypes);
  };

  return (
    <Fragment>
      <Grid item md={4}>
          <div>best sword value</div>
          <div>most merchant exp</div>
          <div>How did I do?</div>

        <Question />
      </Grid>

      <BlueprintTypeModal
        handleFormSubmit={handleItemTypeFormSubmit}
      ></BlueprintTypeModal>

      <Grid item md={4}>
        <Button variant="outlined" color="primary" onClick={handleDialogOpen}>
          Filter Tier
        </Button>

        <Dialog open={isDialogOpen} onClose={handleDialogClose}>
          <DialogTitle id="form-filter-dialog-title">Tier Filter</DialogTitle>

          <DialogContent>
            <TierListForm handleDialogSubmit={handleTierFormSubmit} />
          </DialogContent>
        </Dialog>
      </Grid>

      <BlueprintView tiers={tiers} itemTypes={itemTypes} />
    </Fragment>
  );
};

export default DashboardMainContent;
