import React, { useState, useEffect, Fragment } from "react";

import {
  Button,
  Dialog,
  DialogContent,
  DialogTitle,
  DialogActions,
  FormControlLabel,
  Checkbox,
  FormGroup,
} from "@material-ui/core";

import { Formik, Form, FieldArray } from "formik";
import { fetchItemTypes } from "../model/blueprint-model";

export const BlueprintTypeModal = ({ handleFormSubmit }) => {
  const [blueprintTypes, setBlueprintTypes] = useState([]);
  const [isDialogOpen, setDialogState] = useState(false);
  const [checkedElements, setCheckedElements] = useState([]);

  useEffect(() => {
    loadItemTypes();
  }, [blueprintTypes]);

  const loadItemTypes = async () => {
    const sortedItemTypes = await fetchItemTypes();
    setBlueprintTypes(sortedItemTypes);
    setCheckedElements(sortedItemTypes);
  };

  const getDialogActions = () => {
    return {
      open: () => setDialogState(true),
      close: () => {
        setDialogState(false);
      },
      cancel: () => setDialogState(false),
    };
  };

  const onFormSubmit = (values, actions) => {
    actions.setSubmitting(true);
    handleFormSubmit(values);
    setDialogState(false);
    actions.setSubmitting(false);
  };

  const renderCheckbox = (arrayHelpers, item, index) => {
    return (
      <FormControlLabel
        key={item}
        label={item}
        control={
          <Checkbox
            value={item}
            checked={checkedElements.includes(item)}
            onChange={(e) => {

              console.log(arrayHelpers);

              if (e.target.checked) {
                arrayHelpers.push(item)
              }
              else {
                arrayHelpers.remove(index);
              }

              console.log(arrayHelpers);
              setCheckedElements(arrayHelpers);
            }}
          ></Checkbox>
        }
      ></FormControlLabel>
    );
  };

  return (
    <Fragment>
      <Button
        variant="outlined"
        color="primary"
        onClick={getDialogActions().open}
      >
        Filter Type
      </Button>

      <Dialog open={isDialogOpen} onClose={getDialogActions().close}>
        <DialogTitle>Item Type</DialogTitle>

        <DialogContent>
          {blueprintTypes.length ? (
            <Formik
              initialValues={blueprintTypes}
              onSubmit={(values, actions) => onFormSubmit(values, actions)}
            >
              {({
                values,
                errors,
                touched,
                handleChange,
                handleBlur,
                handleSubmit,
                isSubmitting,
              }) => (
                <Form onSubmit={handleSubmit}>
                  <FieldArray
                    name="blueprintTypes"
                    render={(arrayHelpers) => (
                      <Fragment>
                        <FormGroup>
                          {blueprintTypes.map((itemType, index) => {
                            return renderCheckbox(
                              arrayHelpers,
                              itemType,
                              index
                            );
                          })}
                        </FormGroup>

                        <DialogActions>
                          <div>
                            <Button
                              type="submit"
                              variant="contained"
                              color="primary"
                              disabled={isSubmitting}
                            >
                              Apply Filter
                            </Button>
                          </div>
                        </DialogActions>
                      </Fragment>
                    )}
                  />
                </Form>
              )}
            </Formik>
          ) : (
            <p>Item Types Unavailable</p>
          )}
        </DialogContent>
      </Dialog>
    </Fragment>
  );
};

export default BlueprintTypeModal;
